<template>
  <div class="general">
    <nav-bar></nav-bar>
    <div class="container">
      <div class="card">
        <div class="card-header">Добавление клиента</div>
        <validation-observer ref="observer" v-slot="{ handleSubmit }">
          <b-form @submit.stop.prevent="handleSubmit(save)" @reset="notSave" class="my-form">
            <div v-for="(value,key) in labels" :key="key">
              <div v-if="'registrationAddressCity'===key" class="my-little-block">Адрес регистрации</div>
              <div v-if="'actualAddressCity'===key" class="my-little-block">Адрес фактического проживания</div>
              <div v-if="'phoneNumber'===key" class="my-little-block"></div>
              <b-form-group label-cols-sm="3" :label="labels[key]" label-align-sm="right" class="my-form-group">

                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-if="choices[key]">
                  <b-form-select :value="items[key]" :options="choices[key]" v-model="items[key]"
                                 :state="getValidationState(validationContext)">
                    <template v-slot:first>
                      <b-form-select-option :value="null"></b-form-select-option>
                    </template>
                  </b-form-select>

                </validation-provider>

                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-else-if="['dateOfInterview','DateOfCreationIPSO','ContractPeriod','passDateIssue'].includes(key)">
                  <b-form-input v-model="items[key]" :value="items[key]" type="date"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-else-if="'dod'===key">
                  <b-form-input v-model="items[key]" :value="items[key]" type="date" @change="getAge(items[key])"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-else-if="['age'].includes(key)">
                  <b-form-input v-model="items[key]" :value="items[key]" type="number" min="0"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>
                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-else-if="['adultsCount','minorsCount'].includes(key)">
                  <b-form-input v-model="items[key]" :value="items[key]" type="number" min="0"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false,length:6}" v-slot="validationContext"
                                     v-else-if="['passNumber','registrationAddressIndex','actualAddressIndex'].includes(key)">
                  <b-form-input v-model="items[key]" :value="items[key]" type="number"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false,length:4}" v-slot="validationContext"
                                     v-else-if="'passSeries'===key">
                  <b-form-input v-model="items[key]" :value="items[key]" type="number"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider
                  :rules="{required: false,regex:/^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/}"
                  v-slot="validationContext" v-else-if="key==='phoneNumber'">
                  <b-form-input v-model="items[key]" :value="items[key]" type="tel"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false}" v-slot="validationContext"
                                     v-else-if="['passFromWhomIssue','aboutWork','aboutNoWork'].includes(key)">
                  <b-textarea v-model="items[key]" :value="items[key]" :state="getValidationState(validationContext)"/>
                </validation-provider>

                <validation-provider :rules="{required: false}" v-slot="validationContext" v-else>
                  <b-form-input v-model="items[key]" :value="items[key]" type="text"
                                :state="getValidationState(validationContext)"/>
                </validation-provider>

              </b-form-group>
            </div>
            <button class="btn btn-default" type="reset">Отмена</button>
            <button class="btn btn-default" type="submit">Добавить</button>
          </b-form>
        </validation-observer>
      </div>
    </div>
    <MainFooter v-if="labels"></MainFooter>
  </div>
</template>

<script>
import navBar from "./navBars/navBar";
import {ValidationObserver, ValidationProvider} from "vee-validate/dist/vee-validate.full";
import MainFooter from "./footers/MainFooter";

export default {
  name: "Client",
  components: {
    navBar,
    ValidationProvider,
    ValidationObserver,
    MainFooter
  },
  data() {
    return {
      items: '',
      labels: '',
      choices: '',
    }
  },
  created() {
    $.ajax({
      url: this.$store.state.baseUrl + "api/fields/",
      type: "GET",
      data: {model: 'Client'},
      success: (response) => {
        this.labels = response.data.labels;
        this.choices = response.data.choices;
        this.items = response.data.items;
      },
      error: (response) => {
        if (response.status === 401) this.logOut();
        else alert("Не удалось получить данные с сервера.\nПовторите попытку позже.")
      }
    })
  },
  methods: {
    save() {
      $.ajax({
        url: this.$store.state.baseUrl + "api/client/",
        type: "POST",
        data: this.items,
        context: this,
        success: (response) => {
          this.$router.push({name: 'clientView', params: {id: response.data.id}})
        },
        error: (response) => {
          if (response.status === 401) this.logOut();
          else alert("Не удалось загрузить данные на сервер.\nПовторите попытку позже.")
        }
      });
    },
    notSave() {
      this.$router.push({name: 'mainwindow'})
    },
    getValidationState({dirty, validated, valid = null}) {
      return dirty || validated ? valid : null;
    },
    getAge(dod) {
      var date = new Date(dod);
      var ageDifMs = Date.now() - date.getTime();
      var ageDate = new Date(ageDifMs);
      var dateNow = new Date(Date.now());
      // this.items['age'] = (dateNow.getDate() < date.getDate() ||
      //   dateNow.getMonth() < date.getMonth() ||
      //   dateNow.getFullYear() < date.getFullYear()) ?
      //   -1 : Math.abs(ageDate.getUTCFullYear() - 1970);
      // this.items['age'] = (
      //   dateNow.getFullYear() < date.getFullYear()
      // ) ? -1 : (
      //   (dateNow.getMonth() < date.getMonth()
      //   ) ? -1 : (
      //     (dateNow.getDate() < date.getDate()
      //     ) ? -1 :
      //       Math.abs(ageDate.getUTCFullYear() - 1970))
      // );
      if (dateNow.getFullYear() > date.getFullYear()) {
        this.items['age'] = Math.abs(ageDate.getUTCFullYear() - 1970);
      } else {
        if (dateNow.getFullYear() < date.getFullYear()) {
          this.items['age'] = -1;
        } else {
          if (dateNow.getMonth() > date.getMonth()) {
            this.items['age'] = Math.abs(ageDate.getUTCFullYear() - 1970);
          } else {
            if (dateNow.getMonth() < date.getMonth()) {
              this.items['age'] = -1;
            } else {
              if (dateNow.getDate() >= date.getDate()) {
                this.items['age'] = Math.abs(ageDate.getUTCFullYear() - 1970);
              } else {
                this.items['age'] = -1;
              }
            }
          }
        }
      }
    }
  },
  // computed: {
  //   getAge() {
  //     if (this.items['dod'] !== null) {
  //       var date = new Date(this.items['dod']);
  //       var ageDifMs = Date.now() - date.getTime();
  //       var ageDate = new Date(ageDifMs);
  //       return Math.abs(ageDate.getUTCFullYear() - 1970);
  //     }
  //     return this.items['age'];
  //   }
  // }
}
</script>

<style scoped>
.general {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.container {
  margin-top: 2%;
  text-align: center;
  flex: 1;
}

.card-header {
  display: block;
  font-size: 35px;
  font-weight: 700;
  padding: 35px 0;
  color: #492727;
  background-color: #D2B48C;
  border-color: #D2B48C
}

.card {
  background-color: #f5eed5;
  /*margin-bottom: 5%;*/
}

.btn-default {
  background-color: #D2B48C;
  color: #492727;
  margin: 10px;
}

.btn-default:hover {
  background-color: #452424;
  color: #D2B48C;
}

.my-form {
  background-color: #f5eed5;
}

.my-form-group {
  color: #492727;
  margin-right: 3%;
  /*padding-top: 15px;*/
}

.my-little-block {
  text-align: center;
  color: #492727;
  font-size: 16px;
  padding: 2%;
}
</style>



